th {
  border-bottom: 0.1rem solid #e1e1e1;
  padding: 1.2rem 1.5rem;
  text-align: left;
}

td:first-child,
th:first-child {
  padding-left: 0;
}

td:last-child,
th:last-child {
  padding-right: 0;
}

@media screen and (max-width: 40rem) {
  table {
    border-spacing: 0;
    display: flex;
    width: 100%;
  }
  table thead {
    border-right: solid 0.1rem #e1e1e1;
  }
  table thead td,
  table thead th {
    padding-left: 0;
  }
  table thead td:first-child,
  table thead th:first-child {
    padding-left: 0;
  }
  table thead td:last-child,
  table thead th:last-child {
    padding-right: 1.2rem;
  }
  table tbody {
    display: flex;
    overflow-x: auto;
    white-space: nowrap;
  }
  table tbody tr {
    border-right: solid 0.1rem #e1e1e1;
  }
  table tbody tr:last-child {
    border-right: none;
  }
  table td,
  table th {
    display: block;
  }
  table td:first-child,
  table th:first-child {
    padding-left: 1.2rem;
  }
  table td:last-child,
  table th:last-child {
    padding-right: 1.2rem;
  }
}

b,
strong {
  font-weight: bold;
}

p {
  margin-top: 0;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin-bottom: 2.0rem;
  margin-top: 0;
}

h1 {
  font-size: 4.6rem;
  line-height: 1.2;
}

h2 {
  font-size: 3.6rem;
  letter-spacing: .01rem;
  line-height: 1.25;
}

h3 {
  font-size: 2.8rem;
  letter-spacing: .02rem;
  line-height: 1.3;
}

h4 {
  font-size: 2.2rem;
  letter-spacing: .03rem;
  line-height: 1.35;
}

h5 {
  font-size: 1.8rem;
  letter-spacing: .04rem;
  line-height: 1.5;
}

h6 {
  font-size: 1.6rem;
  line-height: 1.4;
}

img {
  max-width: 100%;
}

.clearfix:after {
  clear: both;
  content: ' ';
  display: table;
}

.float-left {
  float: left;
}

.float-right {
  float: right;
}
